@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    html {
        font-family: "Poppins", sans-serif;
    }

    body {
        background-color: #fdfeff;
        overflow-x: hidden;
    }
}

@layer components {
    .input-box {
        @apply w-full text-sm bg-transparent border-[1.5px] px-5 py-3 rounded mb-4 outline-none;
    }

    .btn-primary {
        @apply w-full text-sm bg-primary text-white rounded my-1 hover:bg-blue-500 py-2;
    }

    .icon-btn {
        @apply text-xl text-slate-300 cursor-pointer hover:text-primary;
    }

    .input-label {
        @apply text-xs text-slate-400;
    }
}
